ox-sizing: inherit;\n}\n\nhtml {\n  height: 100%;\n  box-sizing: border-box;\n}\n\nbody {\n  margin: 0;\n}\n\nimg {\n  max-width: 100%;\n  height: auto;\n}\n\na {\n  text-decoration: none;\n}\n\n.visually-hidden {\n  position: absolute;\n  width: 1px;\n  height: 1px;\n  margin: -1px;\n  padding: 0;\n  border: 0;\n  white-space: nowrap;\n  clip: rect(0 0 0 0);\n  overflow: hidden;\n}\n\n.container {\n    max-width: $desktop-width;\n    margin-left: auto;\n    margin-right: auto;\n    padding-left: 98px;\n    padding-right: 98px;\n}\n","$white: #FFFFFF;\n$background-gray: #F6F7FF;\n$black: #000000;\n$desktop-width: 1366px;\n\n$dark: #1F1E25;\n$blue: #2C36F2;\n$blue-opacity: rgba(44, 54, 242, 0.7);\n$footer-darkgrey: #707C87;\n$line-grey: #C1C2CA;\n$light: #E0E1EA;\n$title-light: #F6F7FF;\n\n$button-shadow: 1px 1px 3px 0px rgba(50, 50, 50, 0.75);\n","@mixin font($weight, $size, $line-height) {\n  font-size: $size;\n  line-height: $line-height;\n  font-weight: $weight;\n}\n\n@mixin pseudo($display: block, $position: absolute, $content: \"\") {\n  display: $display;\n  position: $position;\n  content: $content;\n}\n\n@mixin list {\n  list-style: none;\n  margin: 0;\n  padding: 0;\n}\n\n@mixin reset-indent {\n  margin: 0;\n  padding: 0;\n}\n",".page {\n  @include reset-indent;\n  min-width: $desktop-width;\n  height: 100%;\n  font-family: \"Roboto\", \"Arial\", sans-serif;\n  color: $dark;\n  background-color: $white;\n}\n",".page-header {\n  padding-top: 20px;\n  padding-bottom: 22px;\n  background-color: $background-gray;\n}\n",".logo-img {\n  display: block;\n}\n",".main-nav {\n  @include font(normal, 16px, 19px);\n  display: flex;\n  align-items: flex-start;\n}\n\n.main-nav__logo {\n  display: block;\n  margin-top: 6px;\n  margin-right: 52px;\n}\n",".menu-link {\n  display: block;\n  padding: 15px 5px;\n  color: $dark;\n\n  &:hover,\n  &:focus {\n    color: $blue;\n  }\n\n  &:active {\n    color: $footer-darkgrey;\n   }\n}\n",".main-menu {\n  @include list;\n  display: flex;\n  justify-content: flex-start;\n  flex-wrap: wrap;\n}\n\n.main-menu__item {\n  margin: 0;\n  margin-right: 15px;\n  padding: 0;\n}\n",".user-nav {\n  @include list;\n  display: flex;\n  justify-content: flex-end;\n  flex-wrap: wrap;\n  margin-left: auto;\n}\n\n.user-nav__item {\n  margin: 0;\n}\n\n.user-nav__link {\n  padding-right: 0;\n\n  &--enter {\n    svg {\n      margin-right: 6px;\n      margin-bottom: -7px;\n    }\n  }\n}\n\n.user-nav__link:hover,\n.user-nav__link:focus {\n  path {\n    fill: $blue;\n  }\n}\n\n.user-nav__link:active {\n  path {\n    fill: $footer-darkgrey;\n  }\n}\n",".promo {\n  min-height: 400px;\n  background-color: $blue;\n  background-image:\n    linear-gradient(-180.24deg, rgba(32, 39, 179, 0) 36.31%, #2027B3 99.79%),\n    linear-gradient(-180.24deg, rgba(32, 39, 179, 0) 32.89%, #2027B3 99.79%);\n  background-size:\n    500px 100%,\n    500px 100%;\n  background-position:\n    top 0 left 0,\n    right 0 bottom 0;\n  background-repeat: no-repeat;\n}\n\n.promo__wrapper {\n  padding-top: 102px;\n  padding-bottom: 115px;\n  padding-left: 268px;\n  background-color: $blue;\n  background-image:\n    url(\"../img/cards.png\"),\n    linear-gradient(180.24deg, rgba(32, 39, 179, 0) 36.31%, #2027B3 99.79%),\n    linear-gradient(183.47deg, rgba(56, 65, 255, 0) 66.3%, #3841FF 97.14%),\n    linear-gradient(183.47deg, rgba(56, 65, 255, 0) 27.26%, #3841FF 97.14%),\n    linear-gradient(180.24deg, rgba(32, 39, 179, 0) 32.89%, #2027B3 99.79%);\n  background-size:\n    444px 286px,\n    150px 100%,\n    150px 67%,\n    270px 67%,\n    298px 100%;\n  background-position:\n    top 61px left 699px,\n    top 0 left 0,\n    left 150px bottom 0,\n    left 797px bottom 0,\n    right 0 bottom 0;\n  background-repeat: no-repeat;\n}\n\n.promo__title {\n  @include reset-indent;\n  @include font(700, 54px, 63px);\n  margin-bottom: 8px;\n  color: $title-light;\n}\n\n.promo__slogan {\n  @include reset-indent;\n  @include font(400, 22px, 26px);\n  margin-bottom: 35px;\n  color: $light;\n}\n",".button {\n  display: inline-block;\n  padding: 15px 36px 16px;\n  @include font(500, 18px, 21px);\n  border-radius: 4px;\n\n  &--white {\n    color: $dark;\n    background-color: $white;\n\n    &:hover,\n    &:focus {\n      background-color: $background-gray;\n    }\n\n    &:active {\n      background-color: $light;\n   }\n  }\n\n  &--blue {\n    color: $background-gray;\n    background-color: $blue;\n\n    &:hover,\n    &:focus {\n      background-color: $blue-opacity;\n    }\n\n    &:active {\n      background-color: $footer-darkgrey;\n   }\n  }\n}\n",".converter {\n  padding-top: 45px;\n  padding-bottom: 40px;\n}\n\n.converter__title {\n  @include reset-indent;\n  @include font(700, 41px, 57px);\n  margin-bottom: 50px;\n}\n",".converter-form {\n  @include reset-indent;\n  display: flex;\n  justify-content: space-between;\n  flex-wrap: wrap;\n}\n\n.converter-form__control {\n  height: 60px;\n  margin: 0;\n  margin: 23px 0 60px;\n  font-family: inherit;\n  border: 1px solid $dark;\n  border-radius: 4px;\n  color: $black;\n\n  &--input {\n    @include font(500, 24px, 25px);\n    width: 390px;\n    padding: 13px 40px;\n    text-align: center;\n  }\n\n  &--select {\n    @include font(500, 30px, 31px);\n    position: relative;\n    width: 120px;\n    margin-left: 15px;\n    padding: 12px 16px;\n    appearance: none;\n    background-image: url(\"../img/select-caret.svg\");\n    background-position: right 14px top 23px;\n    background-repeat: no-repeat;\n  }\n\n  &--select::before {\n    top: 179px;\n    left: -120px;\n    background-image: url(\"../img/select-caret.svg\");\n  }\n\n  &--input-date {\n    font-weight: 400;\n    padding-left: 115px;\n    text-align: left;\n  }\n}\n\n.converter-form__fieldset {\n  @include reset-indent;\n  display: flex;\n  border: none;\n}\n\n.converter-form__legend {\n  @include reset-indent;\n  @include font(500, 22px, 31px);\n}\n\n.converter-form__arrows {\n  position: relative;\n\n  &::before, &::after {\n    @include pseudo();\n    width: 53px;\n    height: 18px;\n    background-image: url(\"../img/arrow.svg\");\n  }\n\n  &::before {\n    top: 61px;\n    left: -24px;\n    transform: rotate(180deg);\n  }\n\n  &::after {\n    top: 79px;\n    left: -20px;\n  }\n}\n\n.converter-form__wrapper {\n  position: relative;\n}\n\n.converter-form__calender {\n  position: absolute;\n  top: 27px;\n  right: 18px;\n  width: 43px;\n  height: 46px;\n  padding: 3x;\n  background-color: $white;\n  border: none;\n  background-image: url(\"../img/calender.svg\");\n  background-size: 39px 42px;\n  background-position: center;\n  background-repeat: no-repeat;\n  cursor: pointer;\n\n    &:hover,\n    &:focus {\n      box-shadow: $button-shadow;\n    }\n\n    &:active {\n      top: 28px;\n      right: 17px;\n   }\n}\n\n.converter-form__button {\n  margin: 23px 0 60px auto;\n  padding-left: 108px;\n  padding-right: 100px;\n  border: none;\n}\n",".history {\n  margin: 0 auto 110px;\n}\n\n.history__wrapper {\n  padding: 22px 70px 35px;\n  background-color: $background-gray;\n}\n\n.history__title {\n  @include font(500, 28px, 39px);\n  margin-bottom: 50px\n}\n\n.history__list {\n  @include list;\n  @include font(400, 18px, 23px);\n  display: grid;\n  grid-template-columns: 1fr 1fr;\n  grid-column-gap: 250px;\n  margin-bottom: 20px;\n}\n\n.history__item {\n  position: relative;\n  display: grid;\n  grid-template-columns: 130px 1fr;\n  margin-bottom: 24px;\n\n    &--arrow {\n      display: inline-block;\n      width: 40px;\n      height: 16px;\n      margin-left: 12px;\n      margin-right: 12px;\n      background-image: url(\"../img/arrow_history.svg\");\n      background-repeat: no-repeat;\n    }\n\n  &::after {\n    @include pseudo();\n    top: -35px;\n    right: -130px;\n    width: 1px;\n    height: 100px;\n    background-color: $line-grey;\n  }\n\n  &:nth-child(even)::after {\n    width: 0;\n  }\n}\n\n.history__button {\n  padding: 15px 20px;\n}\n",".footer {\n  background-color: $background-gray;\n  padding-bottom: 60px;\n}\n\n.footer__wrapper {\n  padding-top: 50px;\n  display: grid;\n  grid-template-columns: 250px 150px 198px 150px 1fr;\n  grid-column-gap: 80px;\n}\n\n.footer__logo {\n  display: block;\n  margin-bottom: 15px;\n}\n\n.footer__text {\n  @include font(400, 12px, 17px);\n  margin: 0;\n}\n\n.footer__contact-block {\n  position: relative;\n  margin-top: 10px;\n  padding-left: 27px;\n\n  &::before {\n    @include pseudo();\n    width: 16px;\n    height: 16px;\n    top: 5px;\n    left: -5px;\n    background-position: center;\n    background-repeat: no-repeat;\n  }\n\n  &--mobile::before {\n    background-image: url(\"../img/mobile.svg\")\n  }\n\n   &--phone::before {\n    background-image: url(\"../img/phone.svg\")\n  }\n}\n\n.footer__contact {\n  @include font(500, 16px, 23px);\n  margin: 0;\n  margin-bottom: 3px;\n}\n",".footer-menu {\n  @include list;\n  @include font(500, 16px, 23px);\n  flex-basis: 160px;\n  margin-top: 3px;\n}\n\n.footer-menu__link {\n  color: $dark;\n  padding: 4px 3px;\n}\n",".social {\n  @include list;\n  display: flex;\n  justify-content: flex-end;\n  flex-wrap: wrap;\n  align-items: flex-start;\n  margin: 0;\n  margin-top: 15px;\n\n}\n\n.social__item {\n  @include reset-indent;\n  margin-right: 15px;\n\n  &:last-of-type {\n    margin-right: 0;\n  }\n}\n\n.social__link:hover,\n.social__link:focus {\n  path {\n    fill: $blue;\n  }\n}\n\n.social__link:active {\n  path {\n    fill: $footer-darkgrey;\n  }\n}\n"],"sourceRoot":""}]);
